import * as Yup from 'yup';

export default Yup.object().shape({
  name: Yup.string().required('Nome no informado'),
  father_name: Yup.string().required('Nome do pai no informado'),
  mother_name: Yup.string().required('Nome da me no informado'),
  birth_date: Yup.date()
    .typeError('Data de aniversrio invlida')
    .required('Data de aniversrio informada'),
  nacionality: Yup.string().required('Nacionalidade no informada'),
  birth_city: Yup.string().required('Cidade natal no informada'),
  birth_state: Yup.string().required('Estado natal no informado'),
  gender: Yup.string().matches(/(male|female)/, () => 'Gnero invlido'),
  race: Yup.string().matches(
    /(white|brown|black|indigenous|yellow)/,
    () => 'Raa invlida',
  ),
  ease_relating: Yup.boolean().required(
    'Facilidade em se relacionar no informada',
  ),
  origin_school: Yup.string(),
  healt_plan: Yup.string(),
  food_alergy: Yup.string(),
  medication_alergy: Yup.string(),
  health_problem: Yup.string(),
  special_necessities: Yup.string(),
});
