import * as Yup from 'yup';

export default Yup.object().shape({
  name: Yup.string().required('Nome obrigatrio'),
  birth_date: Yup.date()
    .typeError('Data de aniversrio invlida')
    .required('Data de nascimento obrigatria'),
  nacionality: Yup.string().required('Nacionalidade obrigatria'),
  civil_state: Yup.string().required('Estado civil obrigatrio'),
  profission: Yup.string().required('Profisso obrigatria'),
  cpf: Yup.string().required('CPF obrigatrio'),
  rg: Yup.string().required('RG obrigatrio'),
  address_street: Yup.string().required('Rua obrigatria'),
  address_number: Yup.number().required('Nmero obrigatrio'),
  address_complement: Yup.string(),
  address_neighborhood: Yup.string().required('Bairro obrigatrio'),
  address_city: Yup.string().required('Cidade obrigatria'),
  address_cep: Yup.string().required('CEP obrigatrio'),
  residencial_phone: Yup.string().required('Telefone residencial obrigatrio'),
  commercial_phone: Yup.string().required('Telefone comercial obrigatrio'),
  personal_phone: Yup.string().required('Telefone pessoal obrigatrio'),
  education_level: Yup.string().matches(
    /(elementary_incompleted|elementary_completed|highschool_incompleted|highschool_completed|university_incompleted|university_completed)/,
  ),
  workplace: Yup.string().required('Local de trabalho obrigatrio'),
  monthly_income: Yup.number()
    .typeError('Renda mensal invlida')
    .required('Renda mensal obrigatria'),
  income_tax: Yup.boolean().typeError(
    () => 'Declarao de imposto de renda invlida',
  ),
  email: Yup.string().email('E-mail invlido').required('E-mail obrigatrio'),
  kinship: Yup.string().required('Parentesco obrigatrio'),
});
